.chat-box{
  width: calc(100% - 20px);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 25px;
  background: white;
  height: auto;
  min-height: 50px;
  position: fixed;
  bottom: 10px;
  left: 10px;
  gap: 4px;
padding: 

6px;  
  
  
}
.home{
  
  
  

  width: 100%;
  
  background: linear-gradient(to bottom ,#d0f6f3 30%,#02ada0);
  
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
  height: 100vh;
  padding: 0;
  margin: 0;
  
}
textarea{
  
  height: 40px;
  background: #9cf1e5;
  border-radius: 25px;
  outline: none;
  border: none;
  padding: 10px;
  display: flex;
  font-weight: bold;
  
  font-size: 17px;
}
textarea:focus{
  height: 150px;
}
.options  {
  width: 40px;
  height: 40px;
  
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 0;
  border-radius: 25px;
  
}
.options img{
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.voice{
  width: 40px;
  height: 40px;
  margin: 0 auto 0;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.voice img{
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.send{
  width: 40px;
  height: 40px;
  margin: 0 auto 0;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;border-radius: 25px;
}
.send img{
  width: 30px;
  height:30px;
  blend-mode: screen;
  cursor: pointer;
}



/* ai code */
.massageUser {
  background-color: #d4f1ff;
  padding: 10px;
  margin: 5px;
  border-radius: 25px 25px 0 25px;
  text-align: right;
  width: 280px;
  position: relative;
  font-weight: bold;
  font-size: 18px;
  left: 20%;
  overflow-x: hidden;
}

.massageAi {
  background-color: #f1f1f1;
  padding: 15px;
  font-weight: bold;
  font-size: 18px;
  margin: 10px;
  border-radius: 25px 25px  25px 0px;
  width: 95%;
  line-height: 40px;
  overflow-x: hidden;
  text-align: left;
}

.loading {
  font-style: italic;
  color: gray;
}

.exportBtn {
  padding: 6px 10px;
  margin-left: 10px;
  background-color: #00c897;
  border: none;
  color: white;
  font-size: 12px;
  border-radius: 6px;
  cursor: pointer;
}
